				"html": "<h1 id=\"defining-schemas\"><a href=\"#defining-schemas\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Defining Schemas</h1>\n<p>Schemas are used to define the data structures of different code entities. Schemas might be used to define the structure of a route (path, method, parameters, responses), a model (name, fields), or a page (elementTree, layout).</p>\n<p>Schemas are defined using [JSON Schema] draft-04 (<a href=\"http://json-schema.org/\">http://json-schema.org/</a>). The only hard requirement we impose is that the root schema must be of type <code class=\"language-text\">object</code>. Optic supports an optional extension to the Schema, an <code class=\"language-text\">_order:[]</code> array for objects that allows you to decide the order that fields will appear within Optic. By default, fields appear alphabetically.</p>\n<blockquote>\n<p>You should try to implement common Schemas whenever possible because they make Transformations more powerful. Always check if there are existing Schemas you can use before creating your own. If there is not one that suits your needs, consider adding a Transformation to a more general type (i.e. <code class=\"language-text\">my-high-level-framework/controller</code> -> <code class=\"language-text\">optic:rest/route</code>).</p>\n</blockquote>\n<p>Schemas are defined in markdown with the following annotation. Schema definitions require a code block to appear directly beneath them.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-markdown\"><code class=\"language-markdown\"><span class=\"token comment\">&lt;!-- Schema {\n\t\"id\": \"example\",\n\t\"definition\": {\n\t\t“title”: “Example Schema”,\n\t\t“type”: “object”,\n\t\t“properties”: {...}\n\t}} --></span></code></pre>\n      </div>\n<blockquote>\n<p>“title” within the JSON Schema sets the name used in visual presentations throughout Optic. The “ID” in the annotation sets the name you reference the schema by when defining knowledge</p>\n</blockquote>",
